*::-webkit-scrollbar,
*::-webkit-scrollbar-thumb {
  width: 1.5rem;
  border-radius: 1rem;
  background-clip: padding-box;
  border: 0.5rem solid transparent;
}
*::-webkit-scrollbar-thumb {        
  box-shadow: inset 0 0 0 1rem;
  color: #474747;
}

.mostpop{
    display: flex;
    overflow-x: scroll;
  width: 100%;
    scroll-behavior:smooth;
}

.mostpop::-webkit-scrollbar {
    width: 0.8rem;
   height: 0.5rem;
   margin-top: 1rem;
    margin-top: 1rem; 
    padding: 5rem;
  }

.mostpop::-webkit-scrollbar-track {
    visibility:hidden;
    -webkit-transition: all 5s ease-in-out;
    transition: all 5s ease-in-out;
    background-color: none;
  }

.mostpop::-webkit-scrollbar-thumb {
    background-color: #474747;
    border-radius: 0.5rem;
    border: 0.3rem none #ffffff;
    visibility:hidden;
    
  }
  .mostpop:hover::-webkit-scrollbar-track {
    visibility:visible;
}
.mostpop:hover::-webkit-scrollbar-thumb {
    visibility:visible;
}



