.backcon{
  background-image: url("https://www.xgn.nl/images/articles/2021/202101/_1280x720_crop_center-center_82_line/4509799/Still-Image_Xbox-Game-Pass_2_EA-Play-Title-Cards.f1620123579.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}
.backcon:before{
  content: "";
position: absolute;
width: 100%;
height: 100%;
background-image: linear-gradient(310deg, #121212 ,#121212e7 ,#121212ba , rgba(255, 255, 0, 0));
  
}

.browsecon{
  background-color: red;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
}

.sortcon{
  background-color: red;
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1999;
}
.slick-slide{
  overflow: hidden;
}

.slick-slide img{
  width: 100%;
  object-fit: cover;
  overflow: hidden;
}
.imgslider{
  width: 100%;
  display: flex;
  margin: 1rem 0 1rem 0;
}
.slick-slider,.slick-initialized{
  width:80%;
  justify-content: center;
  margin: auto;
}
.overcon{
  position: relative;
}
.overlay{
  width: 100%;
  height: 100%;
  z-index: 999;
  background: rgb(2,0,36);
background: linear-gradient(180deg, rgba(2,0,36,0) 0%, rgba(18,18,18,0.1) 0%, rgba(18,18,18,1) 100%);
  position: absolute;
  transition: all 5s ease-in-out;
  transition: all 3s ease-in-out;
}
.overlay:hover{
  background: linear-gradient(180deg, rgba(2,0,36,0) 0%, rgba(18,18,18,0.5) 0%, rgba(18,18,18,1) 100%);
}
.overlay2{
  width: 100%;
  height: 100%;
  z-index: 999;
  background: rgb(2,0,36);
background: linear-gradient(180deg, rgba(2,0,36,0) 0%, rgba(18,18,18,0.1) 0%, rgba(18,18,18,1) 100%);
  position: absolute;
  transition: all 5s ease-in-out;
  transition: all 3s ease-in-out;
}
.allcon{
  position: relative;
}
button::before {
  color: white;
}
.slick-active button::before{
  color: white;
  opacity: 1;
}


